import React ,{Component} from "react";
import { Menu } from "antd";
//import '../../../pages/style.scss';
//import StockTable from "./stockTable";
import Table from "./table";
import Sidebar from './sidebar'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
} from "react-router-dom";
import {
  UnorderedListOutlined,
  FilterOutlined,
  ControlOutlined,
} from "@ant-design/icons";
import { Row, Col, Divider, Layout, Card, Button } from "antd";
import DashBoardDesign from "./dashBoardDesign";
import UploadFiles from "./uploadFile";
import LoadingOverlay from 'react-loading-overlay';
import { MDBDataTable } from 'mdbreact';
import {ReportListApi} from '../service/api_url'
import {GetData} from '../service/ApiMethods'

const { Header, Footer, Sider, Content } = Layout;
const { SubMenu } = Menu;
const styles = {
  marginTop: 10,
  background: "rgb(143 215 226)",
  fontFamily: "sans-serif",
  overflow: "hidden",
};

class DashBoard extends Component {

  constructor(props) {  
    super(props);
    this.state={rows:[],isActive:true}
 
  }

  async componentDidMount(){

   GetData(ReportListApi).then(result=>{
    
    this.setState({isActive:false,rows:result.payload})
    console.log(result)

   }).catch(e=>{

      console.log(e)
   })

  }

   columns= [
  
    {
      label: 'Destination_Wallet_ID',
      field: 'Destination_Wallet_ID',
      width: 270,
    },
    {
      label: 'Amount',
      field: 'Amount',
      width: 200,
    },
    {
      label: 'Keyword',
      field: 'Keyword',
      width: 100,
    },
    {
      label: 'Date',
      field: 'Date',
      width: 150,
    },
    {
      label: 'Process_date',
      field: 'Process_date',
      width: 100,
    },
    {
      label: 'Upload_File_Name',
      field: 'Upload_File_Name',
      width: 100,
    },
  
    {
      label: 'Status',
      field: 'Status',
      width: 100,
    }
  ]



  render() {

    let {rows,isActive} = this.state


  return (
    <div className="container-fluid">
      <DashBoardDesign />

      <div>
        <Layout>
  
        <Sidebar />

            <Layout>
              <Header
                className="site-layout-sub-header-background"
                style={{ backgroundColor: "white", padding: 0 }}
              >
                <h2
                  className="text-center"
                  style={{ padding: 5, fontFamily: "ui-sans-serif" }}
                >
                  Report
                </h2>
              </Header>
              <Content
                style={{ margin: "24px 16px 0", backgroundColor: "white" }}
              >

    <LoadingOverlay active={isActive} spinner text='Data is Loading...'>
              <MDBDataTable id="datatable2" style={{fontSize: '12px' }}
                  hover
                  responsive
                    btn
                    
                    data={{columns:this.columns,rows}}
                />

          </LoadingOverlay>
                 
              </Content>
              <Footer style={{ textAlign: "center" }}>
               
              </Footer>
            </Layout>
      
        </Layout>
      </div>
    </div>
  );
 };
}

export default DashBoard;
