import React, { Component,Fragment } from "react";
import "../Pages/style.scss";
import { Form, Button } from "react-bootstrap";
import My_image from "../image/js.png";
import OtpInput from "react-otp-input";
import {PostData} from '../service/ApiMethods'
import {OtpCheckApi} from '../service/api_url'
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const antIcon = <LoadingOutlined style={{ fontSize: 24,color:'white' }} spin />;

class OtpPage extends Component {
  state = {
    otp: "",
    is_pending:false,
    invalid_msg:null
  };


  changehandler = (otp) => this.setState({ otp });

  submithandler = async (event) => {
    event.preventDefault();
    let {otp} = this.state
    if(otp.length !== 6){
      this.setState({invalid_msg:"Invalid OTP"})
      return 0

}
    let mobile = sessionStorage.getItem( 'phonenumber')||null;
    this.setState({is_pending:true,invalid_msg:null})
   
    PostData(OtpCheckApi,{mobile,otp}).then(result=>{

      let data = result.data
      if(data.issuccess){

        sessionStorage.setItem( 'phonenumber',mobile);

        let {payload} = data

        this.props.history.push("/setpassword");

      }else{

        this.setState({is_pending:false})

        if(data.errors){
          toast.error(data.errors.mobile, {position: toast.POSITION.TOP_RIGHT })
        }
        else{
          toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })
        }
        

      }

    }).catch(e=>{
      this.setState({is_pending:false})

      if(e.data){

        let data = e.data
        if(data.errors){
          toast.error(data.errors.otp, {position: toast.POSITION.TOP_RIGHT })
        }
        else{
          toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })

      }

      console.log('e ',data)
    }
    else{
      console.log(e)
    }
    })
  };

  render() {

    let {is_pending,invalid_msg} = this.state

    return (
      <Fragment> <ToastContainer />
      <div className="Home">
        <div className="container">
          <div className="row">
            <div className="col-md-4 m-auto pt">
              <div className="card border-0 bg-white shadow">
                <div className="card-header bg-white text-center p-3">
                  <img
                    src={My_image}
                    alt="Logo"
                    style={{ width: 50, height: 50 }}
                  />
                  <h3>OTP</h3>
                </div>
                <div className="card-body p-3">
                  <Form onSubmit={this.submithandler}>
                    <div className="form-group bg-white text-center">
                      <label
                        className="m-auto pb-2 pt-0"
                        htmlFor="exampleInputEmail1"
                      >
                        Give Your OTP
                      </label>
                      <p style={{fontSize:'12px',color:'red'}}> {invalid_msg?invalid_msg:null}</p>
                      <OtpInput
                        className="m-auto text-center"
                        value={this.state.otp}
                        onChange={this.changehandler}
                        numInputs={6}
                        separator={<span></span>}
                      />
                    </div>
                    <hr />
                    {
                      is_pending?<Button type="submit" className="btn btn-primary bttn" disabled>
                      <Spin indicator={antIcon} /> NEXT
                      </Button>
                      :
                      <Button type="submit" className="btn btn-primary bttn">
                     NEXT
                    </Button>
                    }
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </Fragment>
    );
  }
}
export default OtpPage;
