import React,{useEffect,useState, Fragment} from 'react';
import { Menu } from "antd";
//import '../../../pages/style.scss';
//import StockTable from "./stockTable";
import Table from "./table";
import Sidebar from './sidebar'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
} from "react-router-dom";
import {
  UnorderedListOutlined,
  FilterOutlined,
  ControlOutlined,
  InboxOutlined
} from "@ant-design/icons";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import { Row, Col, Divider, Layout, Card, Button , Upload, message} from "antd";
import DashBoardDesign from "./dashBoardDesign";
import UploadFiles from "./uploadFile";

import {Fackuploadapi,UploadReportApi} from '../service/api_url'
import {PostData} from '../service/ApiMethods'
import LoadingOverlay from 'react-loading-overlay';

const { Dragger } = Upload;

const { Header, Footer, Sider, Content } = Layout;
const { SubMenu } = Menu;
const styles = {
  marginTop: 10,
  background: "rgb(143 215 226)",
  fontFamily: "sans-serif",
  overflow: "hidden",
};

const Fileupload = ()=> {

  const [isActive, setisActive] = useState(false);
  const [loading, setloading] = useState(false);
  const [imageUrl, setimageUrl] = useState('');
  const [certificateinfo, setcertificateinfo] = useState([]);

  function beforeUpload(file) { console.log(file.type)
    const isJpgOrPng = file.type==='application/vnd.ms-excel';
    if (!isJpgOrPng) {
      message.error('You can only upload CSV file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
      message.error('File must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
  }


   function getBase64(img, callback) {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
      }

   const props = {
    name: 'file',
    multiple: false,
    action: Fackuploadapi,
    
    beforeUpload:beforeUpload,

   async onChange(info) 
   {
    if (info.file.status === 'uploading') {
      setloading(true)
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      await getBase64(info.file.originFileObj, imageUrl =>
        { 
          PostData(UploadReportApi,{report_file:imageUrl}).then(result=>{

            result = result.data
            if(result.issuccess){
              toast.success(result.message, {position: toast.POSITION.TOP_RIGHT })
            }
            else{
              toast.error(result.message, {position: toast.POSITION.TOP_RIGHT })
           }
           setisActive(false)
            setloading(false)

          }).catch(e=>{ 
            console.log(e) 
            toast.error(e.data.errors, {position: toast.POSITION.TOP_RIGHT })
            
          setloading(false)})
        }
      )
    }else if (info.file.status === 'error') {
      toast.error(`${info.file.name} file upload failed.`, {position: toast.POSITION.TOP_RIGHT })

    }
  },
  }




  return (
    <div className="container-fluid">
      <DashBoardDesign /> <ToastContainer />

      <div>
        <Layout>
    
        <Sidebar />

            <Layout>
              <Header
                className="site-layout-sub-header-background"
                style={{ backgroundColor: "white", padding: 0 }}
              >
                <h2
                  className="text-center"
                  style={{ padding: 5, fontFamily: "ui-sans-serif" }}
                >
                  Upload File
                </h2>
              </Header>
              <Content
                style={{ margin: "24px 16px 0", backgroundColor: "white" }}
              >


 <LoadingOverlay active={isActive} spinner text='Data is Loading...'>
                <Card
                          className="text-center"
                          style={{
                            marginTop: 10,
                            background: "rgb(231 242 253)",
                            fontFamily: "sans-serif",
                            overflow: "auto",
                          }}
                          bordered={true}
                        >
                          <Dragger {...props}>
                            <p className="ant-upload-drag-icon">
                              <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">
                              Click or drag file to this area to upload
                            </p>
                            <p className="ant-upload-hint">
                              Support for a single or bulk upload. Strictly prohibit from
                              uploading company data or other band files
                            </p>
                          </Dragger>
                        </Card>
 
            </LoadingOverlay>     
              </Content>
              <Footer style={{ textAlign: "center" }}>
               
              </Footer>
            </Layout>
   
        </Layout>
      </div>
    </div>
  );
 };


export default Fileupload;

