import React, { Component,Fragment } from "react";
import "./style.scss";
import { Form, Button,InputGroup,FormControl } from "react-bootstrap";
import My_image from "../image/logonew.png";
import {PostData} from '../service/ApiMethods'
import {MobileCheckApi} from '../service/api_url'
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const antIcon = <LoadingOutlined style={{ fontSize: 24,color:'white' }} spin />;


class Index extends Component {
  state = {
    phone: "",
    invalid_msg:null,
    is_pending:false
  };

  changehandler = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  submithandler = async (event) => {
    event.preventDefault();
    let {phone} = this.state
    if(phone.length !== 10){
          this.setState({invalid_msg:"Input valid phone number"})
          return 0

    }
    this.setState({is_pending:true,invalid_msg:null})

    PostData(MobileCheckApi,{mobile:phone}).then(result=>{

      let data = result.data
      if(data.issuccess){

        sessionStorage.setItem( 'phonenumber',phone);

        let {payload} = data

        if(payload.is_firsttime){

          this.props.history.push("/otp");

        }else{

          this.props.history.push("/password");
        }

      }else{

        this.setState({is_pending:false})

        if(data.errors){
          toast.error(data.errors.mobile, {position: toast.POSITION.TOP_RIGHT })
        }
        else{
          toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })
        }
        

      }

    }).catch(e=>{
      this.setState({is_pending:false})

      if(e.data){

        let data = e.data
        if(data.errors){
          toast.error(data.errors.mobile, {position: toast.POSITION.TOP_RIGHT })
        }
        else{
          toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })

      }
     // toast.error(e, {position: toast.POSITION.TOP_RIGHT })
      console.log('e ',e)
    }
    else{
      console.log('e ',e)
    }
    })

  };

  render() {

    let {invalid_msg,is_pending} = this.state
    return (
      <Fragment> <ToastContainer />
      <div className="Home"> 
        <div className="container">
          <div className="row">
            <div className="col-md-4 m-auto pt">
              <div className="card border-0 bg-white shadow">
                <div className="card-header bg-white text-center p-3">
                  <img
                    src={My_image}
                    alt="Logo"
                    style={{ width: 50, height: 50 }}
                  />
                  <h3>Login</h3>
                </div>
                <div className="card-body p-3">
                  <Form onSubmit={this.submithandler}>

 <label htmlFor="exampleInputEmail1">Phone Number</label>
        <p style={{fontSize:'12px',color:'red'}}> {invalid_msg?invalid_msg:null}</p>
                  <InputGroup className="mb-3">
                  <InputGroup.Prepend>
                    <InputGroup.Text id="basic-addon1">{window.PHONE_PREFIX}</InputGroup.Text>
                  </InputGroup.Prepend>
                    <FormControl
                   type='number'
                    placeholder="xxxxxxxxx"
                    aria-label="Username"
                    required
                    name="phone"
                    onChange={this.changehandler}
                    aria-describedby="basic-addon1"
                  />
                </InputGroup>

                    <hr />
                    {
                      is_pending?<Button type="submit" className="btn btn-primary bttn" disabled>
                      <Spin indicator={antIcon} /> NEXT
                      </Button>
                      :
                      <Button type="submit" className="btn btn-primary bttn">
                     NEXT
                    </Button>
                    }
        
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </Fragment>
    );
  }
}
export default Index;
