import React, { Component,Fragment } from "react";
import "../Pages/style.scss";
import { Form, Button } from "react-bootstrap";
import My_image from "../image/js.png";
import {PostData} from '../service/ApiMethods'
import {SetPasswordApi} from '../service/api_url'
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const antIcon = <LoadingOutlined style={{ fontSize: 24,color:'white' }} spin />;

class PasswordPage extends Component {
  state = {
    Password: "",
    repassword:"",
    is_pending:false,
    invalid_msg:null
  };


  changehandler = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  submithandler = async (event) => {
    event.preventDefault();
  //  this.props.history.push("/");
  let {Password,repassword} = this.state
  if(Password.length <4){
    this.setState({invalid_msg:"Input password is more then 4 character"})
    return 0
  }
  if(Password !== repassword){
    this.setState({invalid_msg:"Password is not match"})
    return 0

}
  let mobile = sessionStorage.getItem( 'phonenumber')||null;
  this.setState({is_pending:true,invalid_msg:null})

  PostData(SetPasswordApi,{mobile,password:Password}).then(result=>{

    let data = result.data

    if(data.issuccess){

        sessionStorage.clear();

      let {payload} = data

      toast.success(data.message, {position: toast.POSITION.TOP_RIGHT })

      setTimeout(function(){ window.location.href ='/'; }, 2000)

    }else{

      this.setState({is_pending:false})

      if(data.errors){
        toast.error(data.errors.mobile, {position: toast.POSITION.TOP_RIGHT })
      }
      else{
        toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })
      }
      

    }

  }).catch(e=>{
    this.setState({is_pending:false})

    if(e.data){

      let data = e.data
      if(data.errors){
        toast.error(data.errors.otp, {position: toast.POSITION.TOP_RIGHT })
      }
      else{
        toast.error(data.message, {position: toast.POSITION.TOP_RIGHT })

    }

    console.log('e ',data)
  }
  else{
    console.log(e)
  }
  })

  
  };

  render() {

    let {is_pending,invalid_msg} = this.state

    return (
        <Fragment> <ToastContainer />
      <div className="Home">
        <div className="container">
          <div className="row">
            <div className="col-md-4 m-auto pt">
              <div className="card border-0 bg-white shadow">
                <div className="card-header bg-white text-center p-3">
                  <img
                    src={My_image}
                    alt="Logo"
                    style={{ width: 50, height: 50 }}
                  />
                  <h3>Login</h3>
                </div>
                <div className="card-body p-3">
                  <Form onSubmit={this.submithandler}>
                    <div className="form-group bg-white text-center">
                      <label htmlFor="exampleInputPassword1">
                        Give Your Password
                      </label>
                      <p style={{fontSize:'12px',color:'red'}}> {invalid_msg?invalid_msg:null}</p>
                      <input
                        type="password"
                        className="form-control"
                        name="Password"
                        placeholder="Password"
                        onChange={this.changehandler}
                        required
                      ></input>

  <label htmlFor="exampleInputPassword15">
                      </label>

                      <input
                        type="password"
                        className="form-control"
                        name="repassword"
                        placeholder="Re-Password"
                        onChange={this.changehandler}
                        required
                      ></input>

                    </div>
                    <hr />
                    {
                      is_pending?<Button type="submit" className="btn btn-primary bttn" disabled>
                      <Spin indicator={antIcon} /> Submit
                      </Button>
                      :
                      <Button type="submit" className="btn btn-primary bttn">
                     Submit
                    </Button>
                    }
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </Fragment>
    );
  }
}
export default PasswordPage;
